import { match } from 'path-to-regexp';
export class RouterScheduler {
    matchPage(pathname, entities) {
        let matchedPage;
        let params = {};
        const matched = entities.some((entity) => {
            if (entity.route === pathname) {
                matchedPage = entity;
                return true;
            }
            // 路由匹配优先级，同expressjs
            params = {};
            matchedPage = entity;
            const hasPage = match(entity.route)(pathname);
            if (hasPage) {
                params = hasPage.params;
                return true;
            }
            return false;
        });
        if (matched) {
            return Object.assign(Object.assign({}, matchedPage), { params, pathname });
        }
        return undefined;
    }
    matchPageByPathname(pathname) {
        const matchedPage = this.matchPage(pathname, this.$entityMap);
        if (!matchedPage) {
            console.warn('Not match route by:', pathname);
            return undefined;
        }
        return matchedPage;
    }
}
