export function find(list, predicate) {
    for (var i = 0; i < list.length; i++) {
        var value = list[i];
        if (predicate(value, i, list)) {
            return value;
        }
    }
}
export function includes(list, searchElement) {
    for (var i = 0; i < list.length; i++) {
        var value = list[i];
        if (value === searchElement) {
            return true;
        }
    }
    return false;
}
